import React from 'react';

const cardContainer = {
	width: "90px",
	height: "85px",
	textAlign: "center",
	display: "flex",
	flexDirection: "column",
	border: "solid",
	borderRadius: "7px"
}

const weatherIcon = {
	align: "center"
}

const SevenDayCard = props => (
	<div style={cardContainer}>
		<div>{props.date}</div>
		<div style={weatherIcon}>{props.weather}</div>
		<div>{props.high} {props.low}</div>
	</div>
);

export default SevenDayCard;