import React from 'react';
import SevenDayCard from './Cards/sevenDayCard';
import { WiDaySunny, WiRain, WiCloudy, WiDayWindy, WiSnow, WiThunderstorm, WiHail } from 'weather-icons-react';

export default class SevenDayForcast extends React.Component {
  render(){
		
		const weatherAppContainer = {
			display: "flex",
			width: "630px",
			height: "100px",
		}
		
		return(
			<div style={weatherAppContainer}>
					<SevenDayCard date="Monday" weather={<WiDaySunny size={24} />} high="13" low="12"/>
					<SevenDayCard date="Tuesday" weather={<WiRain size={24} />} high="21" low="12"/>
					<SevenDayCard date="Wednesday" weather={<WiThunderstorm size={24} />} high="12" low="7"/>
					<SevenDayCard date="Thursday" weather={<WiSnow size={24} />} high="24" low="20"/>
					<SevenDayCard date="Friday" weather={<WiDayWindy size={24} />} high="35" low="15"/>
					<SevenDayCard date="Saturday" weather={<WiHail size={24} />} high="45" low="-35"/>
					<SevenDayCard date="Sunday" weather={<WiCloudy size={24} />} high="-20" low="-25"/>
			</div>
		);
	}
}